<template>
  <v-container>
    <v-card class="mt-10 pa-4" elevation="12">
      <v-card-title
        >

        <v-btn text @click="Onclick_battleStartBtn"> </v-btn>
        <v-btn text @click="Onclick_battleStartBtn"></v-btn>
        <v-btn text @click="test"> </v-btn>
      </v-card-title>

      <v-data-table
        :headers="headers"
        :items="monsters"
        :items-per-page="5"
        :sort-desc="sortDesc"
        :sort-by="sortBy"
        @click:sort="onSort"
      >
        <tr>
          <th></th>
          <th>AC</th>
          <th>HP</th>
        </tr>

        <template v-slot:item="{ item }">
          <tr>
            <td><v-checkbox></v-checkbox></td>
            <td>{{ item.name }}</td>
            <td>{{ item.ac }}</td>
            <td>{{ item.hp }}</td>
            <td>{{ item.nowHp }}</td>
            <td>{{ item.cumulativeDamage }}</td>
            <td>
              <v-text-field
                v-model="item.damageInput"
                @keyup.enter="onDamageInputEnter(item)"
                hide-details="auto"
                class="custom-text-field"
              ></v-text-field>
            </td>
            <td>{{ item.initiative }}</td>
          </tr>
        </template>

        <tr>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "BattleCmp",
  data() {
    return {
      headers: [
        { text: "", value: "", sortable: true },
        {
          text: "",
          align: "start",
          value: "name",
          sortable: false,
        },
        { text: "AC", value: "ac" },
        { text: "HP", value: "hp" },
        { text: " HP", value: "nowHp" },
        { text: "", value: "cumulativeDamage" },
        { text: " ", value: "damageInput" },
        { text: "", value: "initiative", sortable: true },
      ],
      sortBy: "initiative", //   
      sortDesc: false, //    ()
    };
  },
  computed: {
    ...mapGetters("monster", { monsters: "getMonsters" }),
  },
  created() {},
  methods: {
    Onclick_battleStartBtn() {
      //       
      //   initiative 1 20   
      this.monsters.forEach((monster) => {
        const rand = Math.floor(Math.random() * 20) + 1;

        monster.initiative =
          Number(monster.initiative) +
          rand +
          " (" +
          monster.initiative +
          " + " +
          rand +
          ")";

        monster.nowHp = monster.hp;
      });

      //       
      //    
      if (this.sortBy === "initiative") {
        //    'initiative' 
        this.sortBy = "name"; // 'name' 
      } else {
        //   
        this.sortBy = "initiative"; // 'initiative' 
      }
      this.sortDesc = !this.sortDesc; //   
    },
    onSort({ sortBy, sortDesc }) {
      //     
      this.sortBy = sortBy;
      this.sortDesc = sortDesc;
    },
    onDamageInputEnter(monster) {
      //      
      const input = monster.damageInput.trim(); //   
      if (input.startsWith("+")) {
        //  +   
        const damage = parseInt(input.substring(1)) || 0; // +     
        monster.nowHp = Math.min(monster.hp, monster.nowHp + damage); //  hp
        monster.cumulativeDamage -= damage;
      } else {
        //    
        const damage = parseInt(input) || 0; //    0   
        monster.nowHp = Math.max(0, monster.nowHp - damage);
        monster.cumulativeDamage += damage;
      }
      monster.damageInput = ""; //   
    },
    test() {
      confirm("  ?");
    },
  },
};
</script>

<style scoped>
.custom-text-field {
  width: 70px; /*    */
  border-bottom: none !important; /*   */
}
</style>
